import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import WJH from '@/js/wjhJS.js'
import animated from 'animate.css'
import Share from '@/js/shareAndGetName'
import Tool from './js/tool.js'
import Axios from 'axios'

console.log(process.env.NODE_ENV, process.env.VUE_APP_TITLE)

const URL = process.env.NODE_ENV === 'production' && process.env.VUE_APP_TITLE !== 'experiment' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com'

const myAxios = Axios.create({
  baseURL: URL
})

Vue.prototype.WJH = WJH
Vue.prototype.$axios = myAxios

Vue.config.productionTip = false
require('@/js/setSize')
Vue.use(animated)

console.log(
  process.env
)

new Vue({
  router,
  store,
  data () {
    return {
      url: URL,
      share: '',
      showWarning: false
    }
  },
  methods: {
    shareM (title = '小睡眠', desc = '', picUrl) {
      // eslint-disable-next-line
      this.share = new Share({ pic: picUrl, url: window.location.href.split('#')[0], title: title, desc: desc })
      this.share.appShare()
      this.share.rawWeiXinShare(this.url)
    },
    test () {
      const ua = navigator.userAgent.toLowerCase()
      if (Tool.is_cosleep()) {
        this.share.appGetName()
      } else if (/micromessenger/.test(navigator.userAgent.toLowerCase())) {
        this.share.weiXinInit(this.url)
        this.share.weatherCode()
        this.share.weiXinGetName(this.url)
        // eslint-disable-next-line
      } else if (ua.match(/weibo/i) == 'weibo') {
        const a = new Share({ pic: 'asd', url: window.location.href.split('#')[0], title: 'ads', desc: 'asd', isWeiBo: true })
        a.weiXinInit(this.url)
        // 在新浪微博客户端打
      } else {
        this.showWarning = true
      }
    }
  },
  create () {
    // this.shareM('', '', '')
    // this.test()
  },
  mounted () {
    // this.shareM()
  },
  render: h => h(App)
}).$mount('#app')
