<template>
  <div class="home animated fadeIn" :style="home_height">
    <Music class="music" ref="music"/>
    <div class="not-allow-template" v-show="$root.showWarning">
      <div class="template">
        <!-- <img src="../assets/close.png" class="close" /> -->
        <img class="title-bg" :src="star_data.star_info.star_share_bg"/>
        <img src="../assets/top-mask.png" class="mask"/>
        <div class="content">
          <img src="../assets/block.png"/>
          <div>请使用手机QQ、微信、微博，扫码参与活动，为爱豆助力！</div>
          <div @click="$root.showWarning = false" class="light-button">我知道了</div>
        </div>
      </div>
    </div>
    <img class="start-pro" :src="star_data.star_info.star_share_bg"/>
    <div class="content-template">
      <img class="top-mask" src="../assets/top-mask.png"/>
      <div class="fans-avatar">
        <img class="avatar" :src="star_data.fans_info.fans_avatar" />
        <div class="circle" id="circles-1"></div>
      </div>
      <div class="fans-name"></div>
      <div class="help-desc">
        <div class="top">快来帮 <span style="color: RGBA(83, 155, 230, 1)">{{star_data.star_info.star_name}}</span> 点亮星光</div>
        <div class="bottom">享受爱豆暖心哄</div>
      </div>
      <div class="light-button" :class="{ clickBefore: clb }" @click="submitData">{{ clb ? '乖乖去睡' : '为他点亮' }}</div>
      <div class="friend"><span class="special-font">{{star_data.total_user}}</span> 位好友点亮星光 <span class="special-font">{{star_data.total_score}}</span></div>
      <div class="lighten-list-template">
        <div class="list" v-for="(li, index) in star_data.lighten_list" :key="index">
          <div class="left">
            <img :src="li.visitor_avatar" class="avatar"/>
            <div class="name-template">
                <div class="name">{{li.visitor_name}}</div>
                <div class="date">{{parseDate(li.created_at)}}</div>
            </div>
          </div>
          <div class="right">+{{li.visitor_score}}</div>
        </div>
      </div>
      <div class="bottom-desc">小睡眠·COSLEEPER备份</div>
    </div>
    <div class="tips animated fadeIn" v-show="showTips" @click="showTips = false">
      <img class="success-img" :src="tipsImage"/>
    </div>
  </div>
</template>

<script>
import Music from '../components/music.vue'
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
// import MyInput from '../components/Input.vue'
const Circles = require('../js/circles.js')

export default {
  name: 'home',
  data () {
    return {
      clb: true,
      showTips: false,
      tipsImage: '',
      home_height: 0,
      star_data: {
        star_info: {},
        fans_info: {},
        total_user: {},
        lighten_list: {}
      }
    }
  },
  components: {
    Music
  },
  methods: {
    parseDate (v) {
      return this.WJH.dateFormat(+(v + '000'), 'yyyy-MM-dd')
    },
    toRem (v) {
      return +document.querySelector('html').style.fontSize.replace('px', '') * (+v / 37.5)
    },
    circleMounted () {
      const _circle = Circles.default()
      _circle.create({
        id: 'circles-1',
        radius: this.toRem(38),
        value: 21,
        maxValue: 100,
        width: this.toRem(3),
        text: function (value) { return '' },
        colors: ['rgba(255, 255, 255, 0.2)', '#F8C074'],
        duration: 400,
        wrpClass: 'circles-wrp',
        textClass: 'circles-text',
        valueStrokeClass: 'circles-valueStroke',
        maxValueStrokeClass: 'circles-maxValueStroke',
        styleWrapper: true,
        styleText: true
      })
    },
    submitData () {
      // 真正有数据的时候要删除掉
      // this.$root.shareM(`我正在帮${this.star_data.star_info.star_name}`, '求助攻，一起攻上榜首！', '')
      // this.$root.test()
      if (!localStorage.getItem('name') || this.$root.test('avatar')) {
        this.$root.test()
        return
      }
      if (this.clb) {
        return
      }
      this.$axios.post('/web/v1/cosleep/sleep/star/visitor/lighten', { fans_id: this.star_data.fans_info.fans_id, visitor_name: localStorage.getItem('name') || 2, visitor_avatar: localStorage.getItem('avatar') || 2 }).then((res) => {
        const status = res.data.status
        if (status === 1) {
          this.clb = true
          this.showTips = true
          this.tipsImage = res.data.data.visitor_avatar
        }
      })
    },
    getData () {
      this.circleMounted()
      this.$axios.get('/web/v1/cosleep/sleep/star/visitor', { params: { fans_id: 1 } }).then(res => {
        if (res.data.status !== 1) {
          return
        }
        const data = res.data.data
        if (data.today_lighten === 0) {
          this.clb = false
        }
        this.star_data = data
        // this.$root.shareM(`我正在帮${this.star_data.star_info.star_name}`, '求助攻，一起攻上榜首！', data.star_info.star_share_bg)
        // this.$root.test()
        console.log(data.star_info.star_audio)
        this.$refs.music.musicPath = data.star_info.star_audio // require('../assets/a.mp3') // data.star_info.star_audio
        this.$nextTick(() => {
          this.$refs.music.initMusic()
        })
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script>

